import React, { memo, useRef } from "react"
import { Carousel } from "antd"
import type { CarouselRef } from "antd/lib/carousel"
import {
  EditOutlined,
  EllipsisOutlined,
  SettingOutlined,
} from "@ant-design/icons"
import { Avatar, Card } from "antd"
import "./index.less"
const contentStyle: React.CSSProperties = {
  height: "200px",
  color: "#fff",
  lineHeight: "200px",
  textAlign: "center",
  background: "#364d79",
}
const { Meta } = Card
const CenterContent: React.FC = function () {
  const carousel = useRef<CarouselRef>(null)
  const button_left = () => {
    carousel.current?.prev()
  }
  const button_right = () => {
    carousel.current?.next()
  }
  return (
    <div className="content_blog">
      {/*  */}
      {/*  
      draggable:
      arrows
      */}
      <div className="content_button">
        <button onClick={button_left}>{"<"}</button>
        <button onClick={button_right}>{">"}</button>
        <Carousel autoplay draggable ref={carousel}>
          <div>
            <h3 style={contentStyle}></h3>
          </div>
          <div>
            <h3 style={contentStyle}></h3>
          </div>
          <div>
            <h3 style={contentStyle}></h3>
          </div>
          <div>
            <h3 style={contentStyle}>NB</h3>
          </div>
        </Carousel>
      </div>
      <div className="card-tale">
        <Card
          // style={{ width: 1000, height: 300 }}
          // cover={
          //   <img
          //     alt="example"
          //     src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
          //   />
          // }
          actions={[
            <SettingOutlined key="setting" />,
            <EditOutlined key="edit" />,
            <EllipsisOutlined key="ellipsis" />,
          ]}
        >
          <Meta
            avatar={<Avatar src="https://joeschmoe.io/api/v1/random" />}
            title="Card title"
            description="This is the description"
          />
        </Card>
        <Card
          // style={{ width: 1000, height: 300 }}
          // cover={
          //   <img
          //     alt="example"
          //     src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
          //   />
          // }
          actions={[
            <SettingOutlined key="setting" />,
            <EditOutlined key="edit" />,
            <EllipsisOutlined key="ellipsis" />,
          ]}
        >
          <Meta
            avatar={<Avatar src="https://joeschmoe.io/api/v1/random" />}
            title="Card title"
            description="This is the description"
          />
        </Card>
      </div>
      {/*  */}

      <p>ilnafz</p>
      <span>emmo~</span>
    </div>
  )
}
export default memo(CenterContent)
