import React, { useEffect, useState } from "react"
import { Layout, Menu } from "antd"
import HeadContent from "../../components/HeadContent"
import useWindowSize from "../../utils/useWindowSize"
import MENU_CONTENT from "../../constants"

import SiderLeft from "../../components/SiderLeft"
import SiderRight from "../../components/SiderRight"
import CenterContent from "../../components/CenterContent"

import "./index.less"
const { Header, Content, Footer, Sider } = Layout
//
//
//

const HomePage: React.FC = function () {
  const { width } = useWindowSize()
  //headercontent PC
  const [isshow, setIsshow] = useState<Boolean>(true)
  //
  useEffect(() => {
    console.log("")
  })
  useEffect(() => {
    width < 768 ? setIsshow(false) : setIsshow(true)
  }, [width])

  return (
    <Layout className="root-layout">
      <Header
        className="site-layout-sub-header-background"
        style={{ padding: 0 }}
      >
        <HeadContent isshow={isshow}></HeadContent>
      </Header>
      <Layout>
        <Sider
          theme="light"
          className={!isshow ? ".ant-layout-sider-to" : ""}
          breakpoint="md"
          collapsedWidth="0"
          onBreakpoint={(broken) => {}}
          onCollapse={(collapsed, type) => {}}
        >
          <SiderLeft></SiderLeft>
          <div className="logo" />
          <Menu
            theme="light"
            mode="inline"
            defaultSelectedKeys={["4"]}
            items={MENU_CONTENT.map((item, index) => ({
              key: String(index + 1),
              label: item.label,
            }))}
          />
        </Sider>
        <Content style={{ margin: "24px 16px 0" }}>
          <div
            className="site-layout-background"
            style={{ padding: 24, minHeight: 360 }}
          >
            <CenterContent></CenterContent>
          </div>
        </Content>
        {/* trigger={null} tiger */}
        <Sider
          width={300}
          theme="light"
          trigger={null}
          breakpoint="lg"
          collapsedWidth="0"
          onBreakpoint={(broken) => {}}
          onCollapse={(collapsed, type) => {}}
        >
          <SiderRight></SiderRight>
          <></>
          <div className="logo" />
        </Sider>
      </Layout>
      <Footer style={{ textAlign: "center" }}></Footer>
    </Layout>
  )
}
// HomePage
// export default memo(function HomePage() {
//   return <div></div>
// })
export default HomePage
