import React, { memo, useEffect } from "react"
import "./index.less"
interface ChildProcess {
  isshow: Boolean
}
const HeadContent: React.FC<ChildProcess> = function (props) {
  const { isshow } = props
  useEffect(() => {
    console.log(isshow, "")
  }, [isshow])
  return (
    <>
      <div className="head_lft"></div>
      {isshow ? <div className="head_rht"></div> : ""}
    </>
  )
}
export default memo(HeadContent)
